import { forwardRef, useState } from 'react'
import { Container, Typography, Link, Button } from '@mui/material'
import Flex from './Containers/Flex'
import Grid from './Containers/Grid'
import DynamicButton from './Buttons/DynamicButton'
import InfoDialog from './Dialogs/InfoDialog'

import ReactToPrint from 'react-to-print'
import React, { useRef, useEffect } from 'react'

const InfoBar = forwardRef(({ cv }, ref) => {
  const [open, setOpen] = useState(false)
  const instructions = [
    {
      logo: 'add',
      description: 'Add',
      text: (
        <>
          click on the 'Add Section' dropdown and select the section to add,
          duplicate sections are allowed, any section can be renamed and used as
          a template for a custom section
        </>
      ),
    },
    {
      logo: 'edit',
      description: 'Edit',
      text: (
        <>
          click on the section to enter edit mode, click on the button 'Done' or
          click outside to exit edit mode
        </>
      ),
    },
    {
      logo: 'drag',
      description: 'Drag',
      text: (
        <>
          click and hold (or tab on + 'space') the drag dots on the left of the
          section
          <br />
          to confirm: drag in spot and release
          <br />
          to cancel: 'escape' or invalid action
        </>
      ),
    },
    {
      logo: 'delete',
      description: 'Delete',
      text: (
        <>
          click and hold the section until it turns red
          <br />
          to confirm: release while red
          <br />
          to cancel: slide off section, release while not red
        </>
      ),
    },
  ]

  function handleDialogToggle() {
    setOpen(!open)
  }

  const buttonStyling = { fontWeight: '600' }

  return (
    <Grid
      type='between'
      position='fixed'
      zIndex='1'
      top='0'
      width='100%'
      bgcolor='primary.barMain'
      py={0.25}
      boxShadow='0px -12px 30px 0px rgba(125, 137, 248, 0.5)'
    >
      <Container>
        <Flex type='between'>
          <Typography fontSize={14} fontWeight='bold' color='primary.violet'>
            NinjaCV
          </Typography>
          <ReactToPrint
            trigger={() => (
              <DynamicButton
                text='Generate CV'
                mainColor='grey'
                fontWeight='bold'
                sx={buttonStyling}
              />
            )}
            content={() => ref.current}
            pageStyle={
              '@page { size: A4; } @media print { body { -webkit-print-color-adjust: exact; } }'
            }
          />
          <DynamicButton
            type='button info'
            text='Info'
            mainColor='violet'
            onClick={handleDialogToggle}
            sx={buttonStyling}
          />
        </Flex>
      </Container>
      <InfoDialog
        contentArray={instructions}
        open={open}
        close={handleDialogToggle}
      />
    </Grid>
  )
})

export default InfoBar
